<template>
  <!-- a   -->
  <!--  router-link -->
  <component :is="type" v-bind="linkProps(to)">
    <slot />
  </component>
</template>

<script>
import { isExternal } from '@u/validate'

export default {
  props: {
    to: {
      type: String,
      required: true
    }
  },
  computed: {
    /**
     * 
     * computed  
     */
    isExternal() {
      return isExternal(this.to)
    },
    /**
     *  path 
     */
    type() {
      if (this.isExternal) {
        //   a 
        return 'a'
      }
      //  path  vue-router
      return 'router-link'
    }
  },
  methods: {
    /**
     *  attr
     */
    linkProps(to) {
      if (this.isExternal) {
        //   a  attr
        return {
          href: to,
          target: '_blank',
          rel: 'noopener'
        }
      }
      //  path  router-link  attr
      return {
        to
      }
    }
  }
}
</script>

