<template>
  <el-breadcrumb separator="/" class="app-breadcrumb">
    <transition-group name="breadcrumb">
      <el-breadcrumb-item v-for="(item, index) in levelList" :key="item.path">
        <!--  a  span -->
        <!-- () -->
        <!--  -->
        <span v-if="item.redirect === 'noRedirect' || index == levelList.length - 1" class="no-redirect">{{ item.meta.title }}</span>
        <a v-else @click.prevent="handleLink(item)">{{ item.meta.title }}</a>
      </el-breadcrumb-item>
    </transition-group>
  </el-breadcrumb>
</template>

<script>
import { compile } from 'path-to-regexp'

export default {
  data() {
    return {
      levelList: null
    }
  },
  watch: {
    $route(route) {
      // redirect 
      this.getBreadcrumb()
    }
  },
  methods: {
    /**
    * 
    *  route  matched  route meta name, 
    * matched  route meta 
    * 
    *
    */
    getBreadcrumb() {
      let matched = this.$route.matched.filter((ele) => ele.meta && ele.meta.title)
      // matched 
      const firstRoute = matched[0]
      if (!this.isDashboard(firstRoute)) {
        // 
        matched = [{ path: '/dashboard', meta: { title: 'Dashboard' }}].concat(matched)
      }
      // 
      this.levelList = matched.filter((item) => item.meta && item.meta.title && item.meta.breadcrumb !== false)
    },
    /**
    *  dashboard 
    */
    isDashboard(route) {
      //  undefined 
      const name = route && route.name
      if (!name) {
        // false
        return false
      }
      // 
      //  dashboard 
      return name.trim().toLocaleLowerCase() === 'dashboard'
    },
    /**
     * 
     * 
     */
    pathCompile(path) {
      const { params } = path
      // compile  
      const toPath = compile(path)
      console.log(compile, 'pathToRegexp')
      return toPath(params)
    },
    /**
     * 
     * 
     */
    handleLink(route) {
      const { path, redirect } = route
      if (redirect) {
        if (redirect === this.$route.path) {
          //   vue-router 3.2
          // redirect( path ) ===  path  
          //  vue-router@3.1.0  redirect ===  route.path 
          return
        }
        this.$router.push(redirect)
        return
      }
      // 
      this.$router.push(this.pathCompile(path))
    }
  },
  created() {
    this.getBreadcrumb()
  }
}
</script>

<style lang="scss" scoped>
  .app-breadcrumb.el-breadcrumb{
    display: inline-block;
    line-height: 50px;
    font-size: 14px;
    margin-left: 8px;
    .no-redirect {
      color: #97a8be;
      cursor: text;
    }
  }
</style>
