<template>
  <div :class="{'show': show}" class="header-search">
    <svg-icon class-name="search-icon" icon-class="search" @click.stop="click" />
    <el-select
      ref="headerSearchSelect"
      v-model="search"
      :remote-method="querySearch"
      filterable
      remote
      default-first-option
      placeholder="Search"
      class="header-search-select"
      @change="change"
    >
      <el-option v-for="item in options" :key="item.item.path" :value="item" :label="item.item.title" />
    </el-select>
  </div>
</template>

<script>
// fuse.js 
import Fuse from 'fuse.js'
import path from 'path'

export default {
  data() {
    return {
      show: false, // 
      search: '', // 
      fuse: undefined, //  fuse.js
      searchPool: [], // 
      options: [] //  el-option 
    }
  },
  computed: {
    /**
     * route
     */
    routes() {
      return this.$store.getters.permission_routes
    }
  },
  watch: {
    routes() {
      // routes  fuse.js 
      this.searchPool = this.generateRoutes(this.routes)
    },
    searchPool(list) {
      // fuse.js
      this.initFuse(list)
    },
    show(value) {
      if (value) {
        //  show  true 
        document.body.addEventListener('click', this.close)
      } else {
        //  show  false 
        document.body.removeEventListener('click', this.close)
      }
    }
  },
  mounted() {
    //  
    this.searchPool = this.generateRoutes(this.routes)
  },
  methods: {
    /**
     * 
     */
    click() {
      this.show = !this.show
      if (this.show) {
        // 
        this.$refs.headerSearchSelect && this.$refs.headerSearchSelect.focus()
      }
    },
    /**
     * 
     */
    close() {
      this.$refs.headerSearchSelect && this.$refs.headerSearchSelect.blur()
      this.show = false
    },
    /**
     *  change 
     */
    change(val) {
      console.log(val)
      this.$router.push(val.item.path)
      this.search = ''
      this.options = []
      this.$nextTick(() => {
        this.show = false
      })
    },
    /**
     *  fuse.js
     * @param {array} list
     */
    initFuse(list) {
      // Fuse 
      // route
      this.fuse = new Fuse(list, {
        shouldSort: true,
        location: 0,
        threshold: 0.4,
        distance: 800,
        minMatchCharLength: 1,
        keys: [
          {
            name: 'title',
            weight: 0.7
          },
          {
            name: 'path',
            weight: 0.3
          }
        ]

      })
    },
    /**
     *  fuse.js 
     * {
     *  pathstring
     *  title: []
     * }
     */
    generateRoutes(route, basePath = '/', prefixTitle = '') {
      let res = []
      for (const item of route) {
        if (item.hidden) {
          // 
          continue
        }
        // fuse.js 
        const data = {
          path: path.resolve(basePath, item.path),
          title: prefixTitle
        }
        if (item.meta && item.meta.title) {
          // title
          data.title = prefixTitle + item.meta.title
          if (item.redirect !== 'noRedirect') {
            // 
            res.push(data)
          }
        }
        if (item.children) {
          // 
          const dataChil = this.generateRoutes(item.children, item.path, data.title + '>')
          if (dataChil.length >= 1) {
            res = [...res, ...dataChil]
          }
        }
      }
      return res
    },
    /**
     * 
     */
    querySearch(query) {
      // this.fuse.search()
      if (query !== '') {
        this.options = this.fuse.search(query)
      } else {
        this.options = []
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  .header-search {
    font-size: 0 !important;
    .search-icon {
      font-size: 18px;
      cursor: pointer;
      vertical-align: middle;
    }
    .header-search-select {
      width: 0;
      transition: width 0.2s;
      font-size: 18px;
      border-radius: 0;
      overflow: hidden;
      background: transparent;
      vertical-align: middle;
      display: inline-block;
      ::v-deep .el-input__inner{
        border: none;
        border-radius: 0;
        padding-left: 0;
        padding-right: 0;
        box-shadow: none !important;
        vertical-align: middle;
        border-bottom: 1px solid #d9d9d9;
      }
    }
    &.show .header-search-select {
      width: 210px;
      margin-left: 10px;
    }
  }
</style>
