<template>
  <!--   -->
  <!--  -->
  <!--   -->
  <div v-if="!item.hidden">
    <!--  -->
    <template v-if="hasOneShowChild(item.children,item) && (!onlyOneChild.children || onlyOneChild.noShowingChildren) && !item.alwaysShow">
      <app-link v-if="onlyOneChild.meta" :to="resolvePath(onlyOneChild.path)">
        <el-menu-item :index="resolvePath(onlyOneChild.path)">
          <!-- icon and title  -->
          <item
            :icon="onlyOneChild.meta.icon || (item.meta && item.meta.icon)"
            :title="onlyOneChild.meta.title || (item.meta && item.meta.title)"
          />
        </el-menu-item>
      </app-link>
    </template>
    <!--  -->
    <el-submenu v-else ref="subMenu" :index="resolvePath(item.path)" popper-append-to-body>
      <!--  icon -->
      <template slot="title">
        <item
          v-if="item.meta"
          :icon="item.meta && item.meta.icon"
          :title="item.meta.title"
        />
      </template>
      <!--  -->
      <sidebar-item
        v-for="child in item.children"
        :key="child.path"
        :item="child"
        :base-path="resolvePath(child.path)"
        :is-nest="true"
        class="nest-menu"
      />
    </el-submenu>
  </div>
</template>

<script>
import path from 'path'
import AppLink from './link.vue'
import Item from './Item'
import { isExternal } from '@u/validate'

export default {
  name: 'SidebarItem',
  components: {
    AppLink,
    Item
  },
  props: {
    // 
    item: {
      type: Object,
      required: true
    },
    // 
    basePath: {
      type: String,
      default: ''
    },
    // 
    isNest: {
      type: Boolean,
      default: false
    }
  },
  data() {
    /**
     * props  item  onlyOneChild 
     * 
     *   path.resolve item  path 
     *    basePath  item.path 
     *    basePath  (/) item.path "/"
     *     //
     *  return {}  
     */
    this.onlyOneChild = null
    return {
      // onlyOneChild: null
    }
  },
  methods: {
    /**
     * 
     * @param { array } children
     * @param { object } parent
     * @returns Boolean
     */
    hasOneShowChild(children = [], parent) {
      // 
      const showingChildren = children.filter((ele) => {
        if (ele.hidden) {
          return false
        } else {
          this.onlyOneChild = ele
          return true
        }
      })
      // icon 
      if (showingChildren.length === 1) {
        return true
      }
      // icon  
      // router  children  path   paht.resolve  
      // children  0  path 
      if (showingChildren.length === 0) {
        this.onlyOneChild = { ...parent, path: '', noShowingChildren: true }
        return true
      }
      return false
    },
    /**
     * 
     */
    resolvePath(routePath) {
      //  routePath 
      if (isExternal(routePath)) {
        return routePath
      }
      //  basePath 
      if (isExternal(this.basePath)) {
        return this.basePath
      }
      // 
      // console.log('item---', this.item.path, '---only---', this.onlyOneChild.path, '---base---', this.basePath, '---routepath---', routePath)
      return path.resolve(this.basePath, routePath)
    }
  }
}
</script>

