/**
 * 
 * @param { Function } fun 
 * @param { Number } wait 
 * @param { Boolean } immediate 
 * @returns Function
 * 
 * 
 * 
 * 
 *  , setTimout()  later 
 *    later 
 *    
 *       
 *      
 *        
 *          apply(this, args)
 *  fun.apply(this, arg)
 */
export function debounce(fun, wait, immediate) {
  let timeout, timestamp, nowRun, context, res, args
  const later = function() {
    const lastTime = +new Date() - timestamp
    if (lastTime < wait && lastTime > 0) {
      timeout = setTimeout(later, lastTime - wait)
    } else {
      timeout = null
      if (!immediate) {
        res = fun.apply(context, args)
        if (!timeout) timeout = context = null
      }
    }
  }

  return function(...args) {
    context = this
    timestamp = +new Date()
    nowRun = immediate && !timeout
    if (!timeout) {
      timeout = setTimeout(later, wait)
    }
    if (nowRun) {
      res = fun.apply(context, args)
      timeout = context = null
    }
    return res
  }
}

/**
 *
 * @param {*} time
 * @param {*} cFormat
 */
export function parseTime(time, cFormat) {
  if (arguments.length === 0 || !time) {
    return false
  }
  const format = cFormat || '{y}-{m}-{d} {h}:{i}:{s}'
  let date
  if (typeof time === 'object') {
    date = time
  } else {
    if (typeof time === 'string') {
      // 
      if (/^[0-9]+$/g.test(time)) {
        time = parseInt(time)
      } else {
        time = time.replace(new RegExp(/-/gm), '/')
      }
    }
    // 10
    if ((typeof time === 'number') && time.length === 10) {
      time = time * 1000
    }
    date = new Date(time)
  }
  const formatObj = {
    y: date.getFullYear(),
    m: date.getMonth() + 1,
    d: date.getDate(),
    h: date.getHours(),
    i: date.getMinutes(),
    s: date.getSeconds(),
    a: date.getDay()
  }
  const time_str = format.replace(/{([ymdhisa])+}/g, (res, key) => {
    const value = formatObj[key]
    if (key === 'a') {
      return ['', '', '', '', '', '', ''][value]
    }
    return value.toString().padStart(2, 0)
  })
  return time_str
}

